const StyledNavLink = styled(Button)`
  color: inherit;
  text-decoration: none;
  margin-bottom: 20px;
  ${({ theme }) => `
    :hover,
    :active {
      border-bottom: 1px solid ${theme.global.colors.brand.light}
    }
  `}
`

const StyledNavText = styled(Text)`
  position: relative;
  top: -4px;
  margin: 0 10px;
`

const StyledRegisterButtonWrapper = styled(Box)`
  text-align: center;
`
const StyledScriptureText = styled(Text)`
  font-style: italic;
`
const Layout = ({ children, showHeader, showRegisterButton }) => {
  const [openNav, setOpenNav] = useState()
  const [displayRegisterButton, setDisplayRegisterButton] = useState(
    showRegisterButton
  )

  const MainNav = ({ data, direction }) => (
    <Nav direction={direction} fill>
      <StyledNavLink
        onClick={(e) => {
          e.preventDefault()
          navigate('/')
        }}
      >
        <HomeIcon color="brand" />
        <StyledNavText>Home</StyledNavText>
      </StyledNavLink>
      <StyledNavLink
        onClick={(e) => {
          e.preventDefault()
          navigate('/activities')
        }}
      >
        <AttractionIcon color="brand" />
        <StyledNavText>Activities</StyledNavText>
      </StyledNavLink>
      <StyledNavLink
        onClick={(e) => {
          e.preventDefault()
          navigate('/speakers')
        }}
      >
        <GroupIcon color="brand" />
        <StyledNavText>Speakers/Music</StyledNavText>
      </StyledNavLink>
      <StyledNavLink
        onClick={(e) => {
          e.preventDefault()
          navigate(
            'https://www.google.com/maps/dir//Camp+Lebanon+Retreat+Center,+4464+Emmons+Rd,+Oregonia,+OH+45054/@39.4442621,-84.186648,12z/data=!4m8!4m7!1m0!1m5!1m1!1s0x8840f26c0df4b0a7:0x6b20972cafa62441!2m2!1d-84.1166079!2d39.4442831'
          )
        }}
      >
        <DirectionsIcon color="brand" />
        <StyledNavText>Directions</StyledNavText>
      </StyledNavLink>
      <StyledNavLink
        onClick={(e) => {
          e.preventDefault()
          navigate(data.donations)
        }}
      >
        <CurrencyIcon color="brand" />
        <StyledNavText>Give</StyledNavText>
      </StyledNavLink>
    </Nav>
  )

  return (
    <StaticQuery
      query={graphql`
        query siteTitleQuery {
          site {
            siteMetadata {
              title
              description
              keywords
              language
              web_author
              robots
              donations
            }
          }
        }
      `}
      render={(data) => (
        <Grommet theme={theme} cssVars full themeMode="light">
          <Helmet
            title={data.site.siteMetadata.title}
            htmlAttributes={{
              lang: data.site.siteMetadata.language,
            }}
            meta={[
              {
                name: 'description',
                content: data.site.siteMetadata.description,
              },
              { name: 'keywords', content: data.site.siteMetadata.keywords },
              {
                name: 'web_author',
                content: data.site.siteMetadata.web_author,
              },
              { name: 'robots', content: data.site.siteMetadata.robots },
            ]}
          >
            <link
              href="https://fonts.googleapis.com/icon?family=Material+Icons"
              rel="stylesheet"
            ></link>
          </Helmet>
          <Header background="dark-1" pad="medium">
            <ResponsiveContext.Consumer>
              {(responsive) =>
                responsive === 'small' ? (
                  <Button onClick={() => setOpenNav(!openNav)}>
                    <MenuIcon name="menu" color="brand" />
                  </Button>
                ) : (
                  <MainNav data={data.site.siteMetadata} direction="row" />
                )
              }
            </ResponsiveContext.Consumer>
          </Header>
          <Main overflow={{horizontal: "hidden"}}>
            <Stack
              anchor="center"
              margin={{
                bottom: '-6px',
              }}
            >
              <Image src={mastHeadImg} fill />
              <Box align="center" pad="none" width="100vw">
                <Heading level="1" margin="0">
                  <Box align="center">
                    <Image
                      src={logo}
                      alt="Ohio Christian Men's Fellowship"
                      width="75%"
                    />
                  </Box>
                </Heading>
                {showHeader && (
                  <>
                    <Heading level="2" margin="0" color="light-1">
                      Forged and Refined
                    </Heading>
                    <Heading level="3" margin="0" color="light-1">
                      September 10th &amp; 11th 2021
                    </Heading>
                  </>
                )}
              </Box>
            </Stack>
            <Box
              align="center"
              justify="center"
              height={{ min: 'unset' }}
              width={{ min: 'unset' }}
            >
              {children}
              <Waypoint
                onEnter={() => setDisplayRegisterButton(false)}
                onLeave={() => setDisplayRegisterButton(true)}
                topOffset="-20%"
              />
              <Box pad="medium" height={{ min: 'inherit' }}>
                <StyledScriptureText>
                  {`"For the word of God is quick, and powerful, and sharper than any two-
edged sword, piercing even to the dividing asunder of soul and spirit, and of the
joints and marrow, and is a discerner of the thoughts and intents of the heart.” ~ Hebrews 4:12`}
                </StyledScriptureText>
              </Box>
            </Box>
            {openNav && (
              <Layer
                onEsc={() => setOpenNav(false)}
                onClickOutside={() => setOpenNav(false)}
                position="left"
                animation="slide"
                modal={true}
                responsive={false}
                full="vertical"
              >
                <Box
                  flex
                  width="300px"
                  pad="large"
                  elevation="xlarge"
                  background="dark-1"
                >
                  <MainNav data={data.site.siteMetadata} direction="column" />
                </Box>
              </Layer>
            )}
            {displayRegisterButton && (
              <Layer
                open
                position="bottom"
                modal={false}
                responsive={false}
                full="horizontal"
                background="brand"
              >
                <StyledRegisterButtonWrapper
                  flex
                  align="center"
                  justify="center"
                >
                  <Button
                    onClick={(e) => {
                      e.preventDefault()
                      navigate('/registration')
                    }}
                    fill
                  >
                    Register
                  </Button>
                </StyledRegisterButtonWrapper>
              </Layer>
            )}
          </Main>
          {!displayRegisterButton && (
            <Footer background="dark-1" pad="medium">
              <Nav
                direction="row"
                background="brand"
                pad="medium"
                fill
                align="center"
                justify="center"
                round
              >
                <Button
                  href="https://www.facebook.com/ohiochristianmensfellowship/"
                  margin={{ right: 'small' }}
                >
                  <FacebookIcon />
                </Button>
                <Button
                  href="https://www.instagram.com/ohio_christian_mens_fellowship/"
                  margin={{ right: 'small' }}
                >
                  <InstagramIcon />
                </Button>
                <Button href="mailto:ohiochristianmensfellowship@gmail.com">
                  <MailOptionIcon />
                </Button>
              </Nav>
            </Footer>
          )}
        </Grommet>
      )}
    />
  )
}

export default Layout
